<template>
  <v-card title="قائمة النشاطات" flat>
    <v-timeline side="end" v-if="store.placesList.length > 0">
      <v-timeline-item
        v-for="(item, index) in store.placesList"
        :key="index"
        dot-color="primary"
        size="small"
      >
        <v-card color="grey-lighten-4" class="pa-2" rounded="lg" :value="true">
          <v-card-title>{{
            item.name ? item.name : "لا يوجد اسم"
          }}</v-card-title>
          <v-card-text class="text-grey">{{ item.display_name }}</v-card-text>

          <v-card-subtitle>
            <p>عدد الايام : {{ item.quantity ? item.quantity : 0 }}</p>
          </v-card-subtitle>

          <v-card
            class="ma-2 pa-2"
            rounded="lg"
            v-for="i in item.quantity"
            :key="i"
          >
            <v-card-title>اليوم - {{ i }}</v-card-title>
            <v-card-text>
              <div v-for="list in item.activities" :key="list">
                <v-alert
                  class="my-1"
                  :title="list.title"
                  border="start"
                  :text="list.time"
                  variant="tonal"
                  v-if="list.dayNum == i"
                >
                  <p color="text-grey">{{ list.description }}</p>
                </v-alert>
              </div>
            </v-card-text>
            <v-card-actions>
              <v-btn
                @click="openDialog(i, index)"
                color="primary"
                rounded="lg"
                block
                >اضافة نشاط</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-card>
      </v-timeline-item>
    </v-timeline>

    <p class="pt-4 pr-4 text-grey" v-else>لم يتم اختيار مواقع</p>
  </v-card>
  <v-dialog v-model="dialog" persistent width="500">
    <v-card>
      <v-card-title class=""> اضافة نشاط </v-card-title>
      <v-card-text>
        <v-text-field
          variant="outlined"
          label="عنوان النشاط"
          color="primary"
          v-model="activeData.title"
          rounded="lg"
        ></v-text-field>
        <v-text-field
          variant="outlined"
          label=" الوقت"
          color="primary"
          v-model="activeData.time"
          rounded="lg"
          type="time"
        ></v-text-field>
        <v-textarea
          variant="outlined"
          label="تفاصيل النشاط"
          color="primary"
          v-model="activeData.description"
          rounded="lg"
        ></v-textarea>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="error" variant="text" @click="dialog = false">
          اغلاق
        </v-btn>
        <v-btn color="green-darken-1" variant="text" @click="addActive()">
          اضافة
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script setup>
import { onMounted, ref } from "vue";
import { useCounterStore } from "@/store/index.js";

const loading = ref(false);
const store = useCounterStore();

const activitiesList = ref([]);
const dialog = ref(false);

const activeData = ref({
  title: "",
  time: null,
  description: "",
  dayNum: null,
});
function addActive() {
  if (activeData.value.title && activeData.value.time) {
    store.placesList[selectIndex.value].activities.push(activeData.value);
    dialog.value = false;
    activeData.value = {
      title: "",
      time: null,
      description: "",
      dayNum: null,
    };
    selectIndex.value = null;
  }
}

const selectIndex = ref(null);

function openDialog(i, index) {
  selectIndex.value = index;
  activeData.value.dayNum = i;
  dialog.value = true;
}

function editArray() {
  store.placesList = store.placesList.map((place) => {
    return { ...place, activities: [] };
  });
}
editArray();
</script>
