<template>
  <v-container>
    <v-row>
      <v-col cols="12" lg="12" md="12" sm="12" class="text-left">
        <v-btn @click="back()">العودة <v-icon>mdi-arrow-left</v-icon></v-btn>
      </v-col>
      <v-col cols="12" lg="12" md="12" sm="12">
        <v-stepper
          editable
          v-model="currentStep"
          :items="[
            'حدد المواقع',
            'معلومات الرحلة',
            'تحديد جدول زمني',
            'النشاطات',
          ]"
          next-text="التالي"
          prev-text="السابق"
        >
          <template v-slot:[`item.1`]="{}">
            <v-card title="حدد المواقع" flat>
              <Map />
            </v-card>
          </template>
          <template v-slot:[`item.2`]="{}">
            <PrimaryTravelData />
          </template>
          <template v-slot:[`item.3`]="{}">
            <Timetable />
          </template>
          <template v-slot:[`item.4`]="{}">
            <Activities />
            <v-btn
              :loading="loading"
              block
              color="primary"
              class="mt-4"
              @click="addTravelFun()"
              >اضافة الرحلة <v-icon>mdi-plus</v-icon></v-btn
            >
          </template>
        </v-stepper>
      </v-col>
    </v-row>
  </v-container>
</template>
<script setup>
import { onMounted, ref, computed, watch } from "vue";
import { useCounterStore } from "@/store/index.js";
import { useRouter, useRoute } from "vue-router";
import { db } from "../../firebase.js";
import { addDoc, collection, doc } from "firebase/firestore";
import Map from "../../components/map";
import PrimaryTravelData from "../../components/primaryTravelData";
import Timetable from "../../components/timetable";
import Activities from "../../components/activities";
import Swal from "sweetalert2";

const currentStep = ref(1);
const store = useCounterStore();
const router = useRouter();
const loading = ref(false);

store.placesList = [];
store.addTravelData = {
  title: "",
  price: 0,
  creatorUserId: null,
  dateFrom: null,
  dateTo: null,
  description: "",
  imageUrl: "",
  memberNumber: 0,
  places: [],
};

async function addTravelFun() {
  if (store.placesList.length == 0) {
    Swal.fire({
      title: "تاكد من اضافة مواقع ",
      icon: "error",
      timer: 3000,
      position: "top-end",
      toast: true,
      showConfirmButton: false,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });
  } else if (
    store.addTravelData.title &&
    store.addTravelData.dateFrom &&
    store.addTravelData.dateTo &&
    store.addTravelData.imageUrl &&
    store.addTravelData.memberNumber
  ) {
    loading.value = true;

    store.placesList.forEach(async (e) => {
      await store.addTravelData.places.push({
        latitude: e.lat,
        longitude: e.lon,
        nightsStayed: e.quantity?e.quantity : 0,
        placeName: e.name,
        address: e.address,
        activities: e.activities,
      });
    });

    store.addTravelData.creatorUserId = store.userData.uid;
    setTimeout(() => {
      addTravel();
    }, 1000);
  } else {
    Swal.fire({
      title: "تاكد من اضافة معلومات الرحلة ",
      icon: "error",
      timer: 3000,
      position: "top-end",
      toast: true,
      showConfirmButton: false,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });
  }
}
async function addTravel() {
  await addDoc(collection(db, "travelPlans"), store.addTravelData)
    .then((res) => {
      Swal.fire({
        title: "تم الاضافة بنجاح",
        icon: "success",
        timer: 2500,
        position: "top-end",
        toast: true,
        showConfirmButton: false,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener("mouseenter", Swal.stopTimer);
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });
      router.push("/dashboard/myTravelPage");
    })
    .catch((error) => {
      Swal.fire({
        title: "حدث خطا",
        icon: "error",
        timer: 3000,
        position: "top-end",
        toast: true,
        showConfirmButton: false,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener("mouseenter", Swal.stopTimer);
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });
    })
    .finally(() => {
      loading.value = false;
    });
}

function back() {
  router.go(-1);
}
</script>
<style></style>
