<template>
  <v-card title="معلومات الرحلة" flat>
    <v-form ref="formRef">
      <v-row>
        <v-col cols="12" lg="6" md="6" sm="12">
          <v-text-field
            variant="outlined"
            label="عنوان الرحلة"
            color="primary"
            :rules="[rules.required]"
            v-model="store.addTravelData.title"
            rounded="lg"
          ></v-text-field>
        </v-col>
        <v-col cols="12" lg="6" md="6" sm="12">
          <v-text-field
            variant="outlined"
            label="سعر الرحلة للشخص الواحد"
            color="primary"
            :rules="[rules.required]"
            v-model="store.addTravelData.price"
            rounded="lg"
            type="number"
          ></v-text-field>
        </v-col>
        <v-col cols="12" lg="4" md="4" sm="12">
          <v-text-field
            variant="outlined"
            label="التاريخ من"
            color="primary"
            :rules="[rules.required]"
            v-model="store.addTravelData.dateFrom"
            rounded="lg"
            type="date"
          ></v-text-field>
        </v-col>
        <v-col cols="12" lg="4" md="4" sm="12">
          <v-text-field
            variant="outlined"
            label="التاريخ الى"
            color="primary"
            :rules="[rules.required]"
            v-model="store.addTravelData.dateTo"
            rounded="lg"
            type="date"
          ></v-text-field>
        </v-col>
      
        <v-col cols="12" lg="4" md="4" sm="12">
          <v-text-field
            variant="outlined"
            label="عدد الاشخاص في الرحلة"
            color="primary"
            :rules="[rules.required]"
            v-model="store.addTravelData.memberNumber"
            rounded="lg"
            type="number"
          ></v-text-field>
        </v-col>
        <v-col cols="12" lg="12" md="12" sm="12">
          <v-textarea
            variant="outlined"
            label="تفاصيل الرحلة"
            color="primary"
            :rules="[rules.required]"
            v-model="store.addTravelData.description"
            rounded="lg"
          ></v-textarea>
        </v-col>
        <v-col cols="12" lg="12" md="12" sm="12">
          <v-text-field
            variant="outlined"
            label="رابط صورة الرحلة"
            color="primary"
            :rules="[rules.required, rules.url]"
            v-model="store.addTravelData.imageUrl"
            rounded="lg"
            type="url"
          ></v-text-field>
        </v-col>
      </v-row>
      <!-- <v-btn color="success" class="mt-4" block @click="validate"> حفظ </v-btn> -->
    </v-form>
  </v-card>
</template>
<script setup>
import { onMounted, ref } from "vue";
import { useCounterStore } from "@/store/index.js";

const loading = ref(false);

const rules = ref({
  required: (value) => !!value || "الحقل مطلوب",
  min: (v) => v.length >= 8 || "يجب ان يتكون من 8 رموز او اكثر",
  url: (value) => {
    const urlPattern = /^(https?|ftp):\/\/[^\s/$.?#].[^\s]*$/i;
    return urlPattern.test(value) || "يجب أن يكون عنوان URL صالح";
  },
});

const formRef = ref(null);
const store = useCounterStore();

const validate = async () => {
  if (!formRef.value) {
    return;
  }

  const { valid } = await formRef.value.validate();

  if (valid) {
    alert("Form is valid");
  }
};
</script>
