<template>
  <v-card title="تحديد كم ليلة تبقى في كل مكان من القائمة " flat>
    <v-card-subtitle>
      <p v-if="store.addTravelData.dateFrom && store.addTravelData.dateTo">
        تاريخ رحلتك من
        {{
          store.addTravelData.dateFrom
            ? store.addTravelData.dateFrom
            : "لم يتم التحديد"
        }}
        الى
        {{
          store.addTravelData.dateTo
            ? store.addTravelData.dateTo
            : "لم يتم التحديد"
        }} مجموع ليالي الرحلة : {{ numberOfDays - 1 }}
      </p>
      <p v-else >لم يتم تحديد تاريخ للرحلة</p>
    </v-card-subtitle>

    <v-list lines="two" v-if="store.placesList.length > 0">
      <v-list-item
        v-for="(item, index) in store.placesList"
        :key="index"
        :title="item.name ? item.name : 'لا يوجد اسم'"
        :subtitle="item.display_name"
      >
        <template v-slot:append>
          <v-btn
            color="grey-lighten-1"
            icon="mdi-minus"
            variant="text"
            @click="decrement(index)"
          ></v-btn>
          <span>{{ item.quantity || 0 }}</span>
          <v-btn
            color="grey-lighten-1"
            icon="mdi-plus"
            variant="text"
            @click="increment(index)"
          ></v-btn>
        </template>
        <v-divider class="mt-4"></v-divider>
      </v-list-item>
      <div><strong> مجموع الليالي :</strong> {{ totalQuantity }}</div>
    </v-list>
    <p class="pt-4 pr-4 text-grey" v-else>لم يتم اختيار مواقع</p>
  </v-card>
</template>
<script setup>
import {  ref, computed, watchEffect } from "vue";
import { useCounterStore } from "@/store/index.js";

const store = useCounterStore();
const numberOfDays = ref(0);

const increment = (index) => {
  store.placesList[index].quantity =
    (store.placesList[index].quantity || 0) + 1;
    store.placesList[index].activities = []
};

const decrement = (index) => {
  const currentQuantity = store.placesList[index].quantity || 0;
  if (currentQuantity > 0) {
    store.placesList[index].quantity = currentQuantity - 1;
  }
  store.placesList[index].activities = []

};
const totalQuantity = computed(() => {
  return store.placesList.reduce((sum, item) => sum + (item.quantity || 0), 0);
});

watchEffect(() => {
  const start = new Date(store.addTravelData.dateFrom);
  const end = new Date(store.addTravelData.dateTo);
  const timeDifference = end - start;
  const daysDifference = Math.ceil(timeDifference / (1000 * 3600 * 24));
  numberOfDays.value = daysDifference;
});

</script>
