<template>
  <v-row>
    <v-col cols="12" lg="9" md="8" sm="12" xs="12">
      <div ref="map" class="map-container"></div>
    </v-col>
    <v-col cols="12" lg="3" md="4" sm="12" xs="12">
      <v-card :loading="loadingCard">
        <v-list-item
          v-for="(item, index) in pointsWithName"
          :key="index"
          :title="item.name ? item.name : 'لا يوجد اسم'"
          :subtitle="item.display_name"
        >
          <template v-slot:append>
            <v-btn
              color="grey-lighten-1"
              icon="mdi-delete"
              variant="text"
              @click="deleteItem(item, index)"
            ></v-btn>
          </template>
        </v-list-item>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import L from "leaflet";
import "leaflet-routing-machine";
import "leaflet-routing-machine/dist/leaflet-routing-machine.css";
import "leaflet-control-geocoder/dist/Control.Geocoder.css";
import "leaflet-control-geocoder/dist/Control.Geocoder.js";
import Axios from "axios";
import { useCounterStore } from "@/store/index.js";

export default {
  data() {
    return {
      map: null,
      points: [],
      geocoder: null,
      routingControl: null,
      pointsWithName: [],
      loadingCard: false,
      store: useCounterStore(),
    };
  },
  mounted() {
    this.initMap();
    this.addMarkers();
    this.drawRoutes();
    this.setupClickEvent();
    this.setupGeocoder();
  },
  methods: {
    deleteItem(item, index) {
      this.points.splice(index, 1);
      this.pointsWithName.splice(index, 1);
      this.store.placesList = this.pointsWithName;
      this.map.removeLayer([item.lat, item.lon]);
      this.map.remove();
      this.initMap();
      this.addMarkers();
      this.drawRoutes();
      this.setupClickEvent();
      this.setupGeocoder();
    },
    initMap() {
      this.map = L.map(this.$refs.map).setView([33.25719, 44.38952], 10);
      L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png").addTo(
        this.map
      );
    },
    addMarkers() {
      this.points.forEach((point) => {
        L.marker([point.lat, point.lng]).addTo(this.map);
      });
    },
    drawRoutes() {
      const waypoints = this.points.map((point) =>
        L.latLng(point.lat, point.lng)
      );
      this.routingControl = L.Routing.control({
        waypoints,
        routeWhileDragging: false,
        show: false,
      }).addTo(this.map);
    },
    setupClickEvent() {
      this.map.on("click", (event) => {
        this.addMarkerAndPoint(event.latlng);
      });
    },
    addMarkerAndPoint(latlng) {
      const newPoint = { lat: latlng.lat, lng: latlng.lng };

      this.points.push(newPoint);

      L.marker([latlng.lat, latlng.lng]).addTo(this.map);
      this.drawRoutes();
      this.updateWaypoints();
      this.loadingCard = true;

      Axios.get(
        `https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${latlng.lat}&lon=${latlng.lng}`
      )
        .then((res) => {
          this.pointsWithName.push(res.data);
        })
        .finally(() => (this.loadingCard = false));
      this.store.placesList = this.pointsWithName;

      this.store.placesList.forEach((e, index) => {
        this.store.placesList[index].activities = [];
      });
    },
    updateWaypoints() {
      const waypoints = this.points.map((point) =>
        L.latLng(point.lat, point.lng)
      );
      this.drawRoutes();
    },

    setupGeocoder() {
      this.geocoder = L.Control.geocoder({
        defaultMarkGeocode: false,
      })
        .on("markgeocode", (event) => {
          const { center } = event.geocode;
          this.addMarkerAndPoint(center);
        })
        .addTo(this.map);
    },
  },
};
</script>

<style>
.map-container {
  height: 600px;
  cursor: pointer;
}

.leaflet-routing-container {
  display: none;
}
.leaflet-control-geocoder {
  margin-top: 10px;
  margin-right: 10px;
}
</style>
